.often-order {
  padding: 80px 0;
  > div {
    flex-direction: column;
    align-items: center;
    
    .often-order__cards {
      flex-wrap: wrap;
      width: 65rem;
      flex-direction: row;
      .often-order__card {
        transition: 1s ease;
        width: 22%;
        height: 55vh;
        border: 5px solid #673ab7;
        margin: 30px 0;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-start;
        padding: 25px;
        background-position: center;
        background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5)),
          url(../../common/image/card-img.jpg), center;
        background-size: cover;
        overflow: hidden;
        position: relative;
        &:hover {
          transform: scale(1.1); /* Увеличиваем масштаб */
          cursor: pointer;
        }
        > div {
          flex-direction: column;
        }
        p,
        span {
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;
          text-align: center;
          color: #ffffff;
        }
      }
    }
  }
}

