.hero {
  .container {
    margin: 50px auto;
  }
  .hero-description {
    flex-direction: column;
    padding: 40px 0;
    margin-bottom: 50px;
    .p {
      height: 200px;
    }
  }

  .hero-slogan {
    height: 100vh;
    background-image: url("../../common/image/slogan-bg.png");
    background-position: center;
    background-size: cover;
    > div {
      height: 100%;
      flex-direction: column;
      justify-content: space-evenly;
      .about-greeting {
        font-family: "AmaticSC-Regular";
        font-weight: 700;
        font-size: 120px;
        color: #ffffff;
      }
    }
    p {
      font-weight: 400;
      font-size: 60px;
      line-height: 113px;
      text-align: center;
      color: #ffffff;
    }
  }
}
