
.about {
    background-image: url('../common/image/about-page-bg.png');
  }
  .social-network img {
    margin: 10px;
  }
  .social-network img:hover {
    cursor: pointer;
    transform: scale(1.2);
  }
  .contact-info__address {
    flex-direction: column;
    align-items: flex-start;
    padding: 50px 0;
  }
  .contact-info__address > div {
    margin-bottom: 20px;
  }
  .contact-info__address > div > div {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-left: 20px;
  }
  .contact-info > div > p {
    text-align: left;
  }
  .map{
    margin-bottom: 50px;
  }
  .map iframe {
    width: 100%;
  }
  