.empty-wrapper {
    height: 547px;
    width: 50%;
    margin: 100px auto;
    padding-bottom: 100px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
  }
  .empty-wrapper h1 {
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;
    letter-spacing: 0.01em;
    color: #000000;
  }
  .empty-wrapper p {
    font-weight: 400;
    font-size: 18px;
    line-height: 145.4%;
    text-align: center;
    letter-spacing: 0.01em;
    color: #777777;
  }
  