.content {
    padding: 40px 0;
  }
  .content p{
    text-align: left;
  }
  .content__title {
    margin: 35px 0;
  }
  .content__items {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .content__top {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .container {
    width: 90%;
    margin: 0 auto;
  }
  .container--cart {
    max-width: 820px;
    margin: 90px auto;
  }
  .container--cart .content__title {
    margin: 0;
  }
  .cart__top {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .cart .content__title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    font-size: 32px;
  }
  .cart .content__title svg {
    position: relative;
    top: -2px;
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
  .cart .content__title svg path {
    stroke: #232323;
    stroke-width: 1.9;
  }
  .cart__clear {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    cursor: pointer;
    user-select: none;
  }
  .cart__clear span {
    display: inline-block;
    margin-left: 7px;
    color: #b6b6b6;
    font-size: 18px;
  }
  .cart__clear span, .cart__clear svg, .cart__clear path {
    transition: all .15s ease-in-out;
  }
  .cart__clear:hover svg path {
    stroke: #373737;
  }
  .cart__clear:hover span {
    color: #373737;
  }
  .cart__item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    border-top: 1px solid #f6f6f6;
    padding-top: 30px;
    margin-top: 30px;
  }
  .cart__item-img {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    margin-right: 15px;
    width: 10%}
  .cart__item-img img {
    width: 80px;
    height: 80px;
  }
  .cart__item-info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%}
  .cart__item-info h3 {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.01em;
  }
  .cart__item-info p {
    font-size: 18px;
    color: #8d8d8d;
  }
  .cart__item-count {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 13%}
  .cart__item-count-minus svg path:first-of-type {
    display: none;
  }
  .cart__item-count b {
    font-size: 22px;
  }
  .cart__item-price {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 33%}
  .cart__item-price b {
    font-weight: bold;
    font-size: 22px;
    letter-spacing: 0.01em;
  }
  .cart__item-remove {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 4%}
  .cart__item-remove .button {
    border-color: #ddd;
  }
  .cart__item-remove svg {
    transform: rotate(45deg);
  }
  .cart__item-remove svg path {
    fill: #d0d0d0;
  }
  .cart__item-remove .button svg {
    width: 11.5px;
    height: 11.5px;
    position: relative;
  }
  .cart__item-remove .button:hover, .cart__item-remove .button:active {
    border-color: #2a2a2a;
    background-color: #2a2a2a;
  }
  .cart__bottom {
    margin: 50px 0;
  }
  .cart__bottom-details {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: space-between;
  }
  .cart__bottom-details span {
    font-size: 22px;
  }
  .cart__bottom-details span:last-of-type b {
    color: #673ab7;
  }
  .cart__bottom-buttons {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  .cart__bottom-buttons .go-back-btn {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 210px;
    border-color: #ddd;
    text-decoration: none;
  }
  .cart__bottom-buttons .go-back-btn span {
    color: #c3c3c3;
    font-weight: 500;
    font-weight: 600;
  }
  .cart__bottom-buttons .go-back-btn:hover {
    background-color: #111;
    border-color: #111;
  }
  .cart__bottom-buttons .go-back-btn:hover span {
    color: #f6f6f6;
  }
  .cart__bottom-buttons .go-back-btn svg {
    margin-right: 12px;
  }
  .cart__bottom-buttons .go-back-btn svg path {
    fill: transparent;
    stroke-width: 2;
  }
  .cart__bottom-buttons .pay-btn {
    font-size: 16px;
    font-weight: 600;
    width: 210px;
    padding: 16px;
  }