@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";
  @font-face {
  font-family: 'AmaticSC-Regular';
  src: url('./components/fonts/AmaticSC-Regular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
 }   
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  outline: none;
}
body {
  background: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

a,
span,
p,
b,
h1,
h2,
h3,
h4,
h5 {
  color: #232323;
}
h3 {
  font-size: 48px;
  margin-bottom: 20px;
}
h5 {
  font-weight: 600;
  font-size: 28px;
  line-height: 30px;
  color: #ffffff;
  margin-bottom: 20px;
}
a {
  text-decoration: none;
}
p {
  font-weight: 400;
  font-size: 25px;
  line-height: 51px;
  text-align: center;
  color: #000000;
}

.container {
  width: 80%;
  margin: 0 auto;
}
.d-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.delimetr {
  width: 2px;
  height: 50px;
  background-color: #000000;
}
.header {
  height: 100px;
  background: #000000;
  position: sticky;
  top: 0;
  z-index: 7;
}
.header > div {
  height: 100%;
}
.header-links {
  width: 45%;
}
.header-links,
.header-links a,
.about-greeting_links a,
.often-order_btn {
  font-weight: 400;
  font-size: 22px;
  line-height: 0px;
  color: #ffffff;
  text-decoration: none;
}
.header-links_btn,
.about-greeting_links span,
.often-order_btn {
  background: #673ab7;
  width: 220px;
  height: 50px;
  /* padding: 30px 20px; */
  cursor: pointer;
}


.slider {
  padding: 50px 20px;
}
/* ! Slider start*/
.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
}
.clearfix:after {
  clear: both;
}
.hover-text-one {
  overflow: hidden;
  margin: 0 10px;
  width: 90% !important;
  height: 530px;
  list-style: none;
  text-align: center;
  border: 2px solid #673ab7;
  border-radius: 20px;
}


.slick-prev:before, .slick-next:before{
  color: #673ab7;
  font-size: 27px;
  padding: 5px;
}
.hover-text-one figure {
  position: relative;
  z-index: 1;
  display: inline-block;
  overflow: hidden;
  background: #000000;
  text-align: center;
  cursor: pointer;
}
.hover-text-one figure img {
  position: relative;
  display: block;
  min-height: 100%;
  opacity: 0.8;
}
.hover-text-one figure figcaption {
  padding: 2em;
  color: #fff;
  text-transform: uppercase;
  font-size: 1.25em;
  backface-visibility: hidden;
}
.hover-text-one figure figcaption::before,
.hover-text-one figure figcaption::after {
  pointer-events: none;
}
.hover-text-one figure figcaption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hover-text-one figure h3 {
  word-spacing: -0.15em;
  font-weight: 300;
}
.hover-text-one figure h3 span {
  font-weight: 800;
}
.hover-text-one figure h3,
.hover-text-one figure p {
  margin: 0;
}
.hover-text-one figure p {
  letter-spacing: 1px;
  font-size: 68.5%;
}
/* Эффект при наведении */
figure.effect-text-one img {
  width: calc(100% + 100px);
  opacity: 0.7;
  transition: opacity 0.35s, transform 0.35s;
  transform: scale(1.2);
}
.slider-image img {
  width: 100%;
}
figure.effect-text-one figcaption {
  top: auto;
  bottom: 0;
  height: 50%;
  text-align: left;
}
figure.effect-text-one h3,
figure.effect-text-one p {
  transform: translate3d(0, 40px, 0);
}
figure.effect-text-one h3 {
  color: #f66d52 !important;
  transition: transform 0.35s;
}
figure.effect-text-one p {
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  padding: 10px;
  opacity: 0;
  transition: opacity 0.2s, transform 0.35s;
}
figure.effect-text-one:hover img,
figure.effect-text-one:hover p {
  opacity: 1;
}
figure.effect-text-one:hover img,
figure.effect-text-one:hover h3,
figure.effect-text-one:hover p {
  transform: translate3d(0, 0, 0);
}
figure.effect-text-one:hover p {
  transition-delay: 0.05s;
  transition-duration: 0.35s;
}
/* ! Slider end */
.slider-left,
.slider-right {
  cursor: pointer;
}


.footer {
  background-image: url('./components/common/image/footer-bg.png');
  background-position: center;
  background-size: cover;
  height: 80vh;
}
.footer > div {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.footer P {
  color: #fff;
}
/* Button scc */
.button {
  display: inline-block;
  background-color: #673ab7;
  border-radius: 10px;
  padding: 10px 20px;
  min-width: 100px;
  text-align: center;
  cursor: pointer;
  transition: background-color .15s ease-in-out, border-color .15s ease-in-out;
  border: 1px solid transparent;
  user-select: none;
}
.button, .button span {
  color: #fff;
}
.button i, .button span, .button path, .button svg {
  transition: all .15s ease-in-out;
}
.button:hover {
  background-color: #35146f;
}
.button:active {
  background-color: #2d0375;
  transform: translateY(1px);
}
.button--circle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  min-width: 32px;
  padding: 0;
  border-width: 2px;
}
.button--black {
  background-color: #232323;
}
.button--black:hover, .button--black:active {
  background-color: #3d3d3d;
}
.button--outline {
  background-color: #fff;
  border-color: #673ab7;
}
.button--outline, .button--outline span {
  color: #673ab7;
}
.button--outline svg path {
  fill: #673ab7;
}
.button--outline:hover {
  background-color: #673ab7;
}
.button--outline:hover, .button--outline:hover span {
  color: #fff;
}
.button--outline:hover svg path {
  fill: #fff;
}
.button--outline:active {
  background-color: #2f2048;
}
.button__delimiter {
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.25);
  margin-left: 14px;
  margin-right: 14px;
}
.button--add svg {
  margin-right: 2px;
}
.button--add span {
  font-weight: 600;
  font-size: 16px;
}
.button--add:hover i {
  background-color: #fff;
  color: #673ab7;
}
.button--add i {
  display: inline-block;
  border-radius: 30px;
  background-color: #673ab7;
  color: #fff;
  font-weight: 600;
  width: 22px;
  height: 22px;
  font-style: normal;
  font-size: 13px;
  line-height: 22px;
  position: relative;
  top: -1px;
  left: 3px;
}
.button--cart {
  display: flex;
  align-items: center;
  line-height: 23px;
  padding: 12px 25px;
}
.button--cart svg {
  margin-right: 8px;
  margin-bottom: 1px;
}
.button--cart span {
  font-weight: 600;
  font-size: 16px;
}